<template>
  <div style="background:#f6f6f6">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(item,k) in getLunboList" :key="k">
        <img class="sw" :src="item.pic_path" alt />
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
      <img class="swiper-button-prev" src="../assets/image/back.png" slot="button-prev" alt />
      <img class="swiper-button-next" src="../assets/image/back.png" slot="button-next" alt />
    </swiper>
    <!--  -->
    <div class="hot bgcolorf6">
      <h4 class="font40">
        
        <span class="theme"></span>
      </h4>
      <div class="flex colord2">
        <div class="font20 flex">
          <div class="line"></div>
        </div>
        <div class="txt">HOT SELLING</div>
        <div class="font20 flex">
          
          <div class="line"></div>
        </div>
      </div>
      <p class="colord2"></p>
      <div class="hotnav">
        <span
          v-for="(item,index) in hotnavList"
          :key="index"
          :class="{'hot_active':item===type}"
          @click="handhotnav(item)"
        >{{item}}</span>
      </div>
    </div>
    <!--  -->
    <div>
      <transition-group class="parent" name="bounce" mode="out-in">
        <hotlist :info="item" v-for="item in hotlist" :key="item.id"></hotlist>
      </transition-group>
    </div>
    <!--  -->
    <swiper :options="swiperOption">
      <swiper-slide v-for="(item,k) in getLunboList" :key="k">
        <div class="designer">
          <div class="designer_box">
            <div class="designer_main">
              <h5></h5>
              <div class="h5_bot">DESIGN TEAM</div>
              <div class="h5_bot_div"></div>
              <div class="h5_bot_div_div">Build a nice and practical villa for you..</div>
              <div class="dashed font24"></div>
              <div class="dash">
                
                <span>Bksnnk</span>
              </div>
              <span class="liaojie font20 poniter" @click="handLearn"></span>
              <div class="resevers">
                <div></div>
                <div class="fuwu">
                  <span></span>
                </div>
              </div>
              <div class="fl_ar technological">
                <div v-for="(item,index) in design" :key="index" class="technological_design fl_ar">
                  <div class="borderf fl_center">
                    <img :src="item.img" alt />
                  </div>
                  <span>{{item.name}}</span>
                </div>
              </div>
            </div>
            <div class="picbox fl_be_al">
              <img :src="item.pic_path" alt />
              <div class="fl_be_al img_pic">
                <img :src="item.pic_path" alt />
                <img :src="item.pic_path" alt />
              </div>
            </div>
          </div>
        </div>
      </swiper-slide>
    </swiper>
    <!--  -->
    <div class="hot bgcolorf6">
      <h4 class="font40">
        
        <span class="theme"></span>
      </h4>
      <div class="flex colord2">
        <div class="font20 flex">
          <div class="line"></div>
        </div>
        <div class="txt">PRESELL</div>
        <div class="font20 flex">
          
          <div class="line"></div>
        </div>
      </div>
      <p class="colord2"></p>
    </div>
    <div class="presell">
      <div class="presell_child">
        <img
          class="img"
          v-for="(item,index) in morelist"
          :key="index"
          :src="item.imgs"
          alt=""
          @click="handprodetail(item)"
        />
      </div>
    </div>
    <div class="presell_bottom">
      <div class="more poniter" @click="handmore" v-if="morelist.length<hotdata.length"></div>
      <div class="more poniter" v-else></div>
    </div>

    <!--  -->
    <div class="hot bgcolorf6">
      <h4 class="font40">
        
        <span class="theme"></span>
      </h4>
      <div class="flex colord2">
        <div class="font20 flex">
          <div class="line"></div>
        </div>
        <div class="txt">SERVICE PROCESS</div>
        <div class="font20 flex">
          
          <div class="line"></div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="posfuwu">
      <div class="service">
        <div class="service_line"></div>
        <div class="cicle">
          <span>1</span>
          <span>2</span>
          <span>3</span>
          <span>4</span>
          <span>5</span>
        </div>
      </div>
      <!--  -->
      <div class="process">
        <div v-for="(item,index) in serviceList" :key="index" class="process_item">
          <div class="fdae31">
            <img :src="item.img" alt />
          </div>
          <h6 class="h6">{{item.name}}</h6>
          <li class="color98">{{item.name1}}</li>
          <li class="color98">{{item.name2}}</li>
          <li class="color98">{{item.name3}}</li>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="video">
      <div class="hot bgcolorf6 vdeodiv">
        <h4 class="font40">
          
          <span class="theme"></span>
        </h4>
        <p class="colord2 vdeop"></p>
      </div>
      <video
        :src="homeList.home_video"
        controls
        poster="../assets/image/vdeo.png"
        width="1200"
        height="676"
        preload="none"
      ></video>
      <div class="vedoimg" @click.stop="handplay">
        <img src="../assets/image/player.png" alt v-if="vdeoimg" />
      </div>
    </div>
    <!--  -->
    <div class="hot bgcolorf6">
      <h4 class="font40">
        
        <span class="theme"></span>
      </h4>
      <div class="flex colord2">
        <div class="font20 flex">
          <div class="line"></div>
        </div>
        <div class="txt">INFORMATION</div>
        <div class="font20 flex">
          
          <div class="line"></div>
        </div>
      </div>
      <p class="colord2"></p>
      <div class="hotnav">
        <span
          v-for="(item,index) in inforList"
          :key="index"
          :class="{'hot_active':item===typeinfor}"
          @click="handInfor(item)"
        >{{item}}</span>
      </div>
    </div>
    <!--  -->
    <div class="mationmain">
      <transition name="bounce" mode="out-in" class="mationparent">
        <div class="inforparent" v-for="(item,index) in list1 " :key="index">
          <div>
            <div class="inforlist">
              <img src="../assets/image/1.png" alt />
              <div class="infor_txt">
                <div class="b">
                  12{{item}}
                  <span>/month</span>
                </div>
                <span class="hline"></span>
                <h6 class="h6tit"></h6>
                <span class="hline1"></span>
                <p class="txtp two-wrap"></p>
              </div>
              <div class="blok"></div>
            </div>
            <!--  -->
            <div class="infornews_parent">
              <div class="infornews" v-for="(item,index) in inList" :key="index">
                <p class="txtp1 one-wrap">{{item.infotxt}}</p>
                <div>{{item.time}}</div>
              </div>
            </div>
          </div>
        </div>
      </transition>
      <!--  -->
      <div class="btn">
        <button @click="handprve">
          <span class="iconfont icon-jiantou"></span>
        </button>
        <button @click="handnext">
          <span class="iconfont icon-arrow_right"></span>
        </button>
      </div>
    </div>
    <!--  -->
    <div style="padding-bottom:40px">
      <div class="hot bgcolorf6">
        <h4 class="font40">
          
          <span class="theme"></span>
        </h4>
        <div class="flex colord2">
          <div class="font20 flex">
            <div class="line"></div>
          </div>
          <div class="txt">Map display</div>
          <div class="font20 flex">
            
            <div class="line"></div>
          </div>
        </div>
        <p class="colord2"></p>
      </div>
      <div class="mapshow">
        <div class="txt1">2651030</div>
        <div class="txt2">2000+</div>
        <img src="../assets/image/map.png" alt />
      </div>
    </div>
    <!--  -->
    <div class="fixed">
      <div @click="goqq">
        <img src="../assets/image/fixed/kf.png" alt />
        <span></span>
      </div>
      <div>
        <img src="../assets/image/fixed/erwm.png" alt />
        <span></span>
      </div>
      <div @click="handorder">
        <img src="../assets/image/fixed/ddan.png" alt />
        <span></span>
      </div>
      <!-- <div>
        <img src="../assets/image/fixed/gwuc.png" alt />
        <span></span>
      </div>-->
      <div @click="handtop">
        <img src="../assets/image/fixed/fhdb.png" alt />
        <span></span>
      </div>
    </div>
  </div>
</template>

<script>
import hotlist from '@/components/hostList.vue'
import { mapState } from 'vuex'
import request from '@/request.js'
import axios from 'axios'
export default {
  components: {
    hotlist,
  },
  data() {
    return {
      swiperOption: {
        pagination: {
          el: '.swiper-pagination',
          clickable: true, // 
        },
        autoplay: {
          delay: 3000,
          disableOnInteraction: false, // 
        },
        loop: true,
        navigation: {
          nextEl: '.swiper-button-next', // 
          prevEl: '.swiper-button-prev', // 
        },
      },
      hotdata: [],
      hotnavList: ['', '', '', ''],
      getLunboList: [], //
      serviceList: [
        {
          img: require('../assets/image/liucheng/kf.png'),
          name: '',
          name1: '',
          name2: '',
          name3: '',
        },
        {
          img: require('../assets/image/liucheng/dj.png'),
          name: '',
          name1: '',
          name2: '',
          name3: '',
        },
        {
          img: require('../assets/image/liucheng/sjs.png'),
          name: '',
          name1: '',
          name2: '',
          name3: '',
        },
        {
          img: require('../assets/image/liucheng/ht.png'),
          name: '',
          name1: '',
          name2: '',
          name3: '',
        },
        {
          img: require('../assets/image/liucheng/ys.png'),
          name: '',
          name1: '',
          name2: '',
          name3: '',
        },
      ],
      type: '',
      morelist: [], // 
      idx: 2,
      inforList: [
        '',
        '',
        '',
        '',
        '',
        '',
      ],
      typeinfor: '',
      inList: [
        {
          infotxt:
            '',
          time: '19 - 12 - 11',
        },
        {
          infotxt:
            '',
          time: '19 - 12 - 11',
        },
        {
          infotxt:
            '',
          time: '19 - 12 - 11',
        },
        {
          infotxt:
            '',
          time: '19 - 12 - 11',
        },
      ],
      list: [1, 2, 3],
      list1: [],
      design: [
        {
          img: require('../assets/image/shejishi/jiaoliu.png'),
          name: '',
        },
        {
          img: require('../assets/image/shejishi/vip.png'),
          name: '',
        },
        {
          img: require('../assets/image/shejishi/xgtu.png'),
          name: '',
        },
        {
          img: require('../assets/image/shejishi/sgtu.png'),
          name: '',
        },
        {
          img: require('../assets/image/shejishi/sdfahuo.png'),
          name: '',
        },
        {
          img: require('../assets/image/shejishi/sg.png'),
          name: '',
        },
      ],
      vdeoimg: true,
      homeList:[]
    }
  },
  watch: {},
  computed: {
    hotlist: {
      get: function () {
        let list = [...this.hotdata]
        list = list.filter((item) => item.style === this.type)
        return list
      },
      set: function () {},
    },
  },
  created() {
    this.list1 = this.list.slice(0, 1)
    //
    request
      .getHots({
        page: 1,
        style: this.type,
      })
      .then((res) => {
        this.hotdata = res.data
        this.morelist = [...this.hotdata.slice(0, 3)]
      })
      .catch((e) => {})
      .finally(() => {})

      // pc
    request.getHomeindex({
    }).then(res=>{
      this.homeList=res.data
      console.log(res,'pc')
    }) .catch((e) => {})
      .finally(() => {})


    this.getzixun()
    this.getprell()
    this.getlunbo()
  },
  methods: {
    gethots() {
      //
      request
        .getHots({
          page: 1,
          style: this.type,
        })
        .then((res) => {
          this.hotdata = res.data
        })
        .catch((e) => {})
        .finally(() => {})
    },
    getzixun() {
      //
      request
        .getHomebaike({
          page: 1,
        })
        .then((res) => {
          console.log(res, '')
        })
        .catch((e) => {})
        .finally(() => {})
    },

    getprell() {
      //
      request
        .getPresell({
          page: 1,
        })
        .then((res) => {
          console.log(res, '')
        })
        .catch((e) => {})
        .finally(() => {})
    },

    getlunbo() {
      //
      request
        .getLunbo({ type: 1 })
        .then((res) => {
          this.getLunboList = res.data.list
        })
        .catch((e) => {})
        .finally(() => {})
    },

    // 
    handhotnav(nav) {
      let list = [...this.hotdata] // 
      list = list.filter((item) => item.style === nav)
      this.type = nav
      this.gethots()
      this.hotlist = list
    },

    // 
    handInfor(nav) {
      let list = [...this.hotdata] // 
      list = list.filter((item) => item.type === nav)
      this.typeinfor = nav
      this.hotlist = list
    },

    // 
    handmore() {
      // 
      this.idx++
      let list = [...this.morelist]
      this.morelist = [...list, ...this.hotdata.slice(this.idx++, this.idx + 3)]
    },
    handprve() {
      let first = this.list.shift()
      this.list.push(first)
      this.list1 = this.list.slice(0, 1)
    },
    handnext() {
      console.log(1)
      let last = this.list.pop()
      this.list.unshift(last)
      this.list1 = this.list.slice(0, 1)
    },
    // 
    handLearn() {
      this.$router.push({
        path: '/teamDetail',
      })
    },
    handtop() {
      //
      window.scrollTo(0, 0)
    },
    handprodetail(item) {
      //
      let idname=(item.id)
      this.$router.push({
        path: '/productDetail',
        query:{
          id:idname
        }
      })
    },
    handorder() {
      //
      this.$router.push({
        path: '/order',
      })
    },
    handplay() {
      //
      let vdeoimg = document.querySelector('.vedoimg>img')
      let video = document.querySelector('video')
      if (video.paused) {
        video.play()
        this.vdeoimg = false
      } else {
        video.pause()
        this.vdeoimg = true
      }
    },

    // 

    goqq(){

    }
  },
  mounted() {
    let nav = document.querySelector('.nav')
    nav.style.display = 'block'
  },
}
</script>

<style scoped>
@import '../assets/index.css';
.vdeodiv {
  padding-bottom: 0;
}
.vdeop {
  margin: 12px auto;
}
.video {
  position: relative;
}
.video .vedoimg {
  height: 594px;
  position: absolute;
  top: 134px;
  left: 0;
  width: 1200px;
}
.video .vedoimg img {
  width: 92px;
  height: 92px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -46px;
  margin-left: -46px;
}
</style>



